import * as React from 'react';
import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import Layout from '../components/layout';
import Seo from '../components/seo';
import * as style from '../styles/style.module.css';

const IndexPage = () => {
  return (
    <Layout pageType="topPage" pageTitle="ツキノラボは、月の森デザインの Web研究室です">
      <Seo topPage={true} />
      <section className={style.topBox}>
        <h2 className={style.topBox__title} data-subtitle="Shortcuts to frequently used memorandum.">
          よく使うメモ
        </h2>
        <div className={style.quickBox}>

          <div className={style.quick}>
            <Link to="/memo/emmet" className={style.quick__link}>
              <figure className={style.quickThumbnail}>
                <StaticImage
                  className={style.quickThumbnail__img}
                  alt="Emmetメモ。"
                  src="../../content/posts/emmet/post-emmet.png"
                  placeholder="Blurred"
                />
              </figure>
            </Link>
          </div>

        </div>
      </section>


      {/* <section className={style.topBox}>
        <h2 className={style.topBox__title} data-subtitle="What you are currently interested in.">
          興味があること
        </h2>
        <div className={style.quickBox}>

          <div className={style.quick}>
            <Link to="/tag/React" className={style.quick__link}>
              <figure className={style.quickThumbnail}>
                <StaticImage
                  className={style.quickThumbnail__img}
                  alt="Reactに興味があるよ。"
                  src="../img/tag-react.png"
                  placeholder="Blurred"
                />
              </figure>
            </Link>
          </div>

        </div>
      </section> */}


      <section className={style.topBox}>
        <h2 className={style.topBox__title} data-subtitle="A collection of websites created as a trial">制作したサイト</h2>
        <div className={style.mediaBox}>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/11/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  FizzBuzzy
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="FizzBuzzy｜FizzBuzzゲーム。"
                      src="../img/created-site11.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>FizzBuzzを Webゲームにしました。「Fizz」や「Buzz」などのボタンを押して、落ちてくる数字を消すだけの簡単なゲームです。Canvasを使用しています。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/10/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  カラーコードつくーる
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="Sass変数=カラーコードつくーる"
                      src="../img/created-site10.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>Sass用のコードリストを作成するためのツールです。変数名と 16進数カラーコードを出力します。色は日本の伝統色（和色）を使用。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/9/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  日付リストつくーる
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="日付リストつくーる｜日付リスト作成ツール"
                      src="../img/created-site9.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>日付リストを作成するWebツールです。1ヶ月分の日付を、テキストエディタなどに貼り付けて使用します。作業ログを書くときに便利なので制作しました。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/8/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  simpleCalculatorβ
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="simpleCalculatorβ"
                      src="../img/created-site8.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>電卓のWebツールです。四則演算しかできない少機能シンプル設計。残念ながら、小数点計算が怪しい β（ベータ）版。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/7/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  JavaScript superStars
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="JavaScript superStars | GitHub great repository Top 10"
                      src="../img/created-site7.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>GitHubの人気リポジトリ 年間 Top10を表示する Webサイトです。カテゴリは JavaScript。スター数が多いリポジトリ順で表示。Fetch APIを使用しました。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/6/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  qNewest
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="qNewest｜Qiitaの最新記事 フロントエンドエンジニア向け"
                      src="../img/created-site6.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>Qiitaの最新記事を表示する Webサイトです。HTML・CSSなどのカテゴリごとに表示できます。XMLHttpRequestを使用しました。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/5/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  ストップウォッチ
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="qNewest｜Qiitaの最新記事 フロントエンドエンジニア向け"
                      src="../img/created-site5.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>ストップウォッチの Webツールです。ラップタイム機能付き。一定区間ごとの経過時間を測ることができます。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/4/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  ToDoList やること帳
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="ToDoList やること帳"
                      src="../img/created-site4.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>ToDoリストの Webツールです。追加、編集・更新、削除、それと作業が済んだ項目にはチェックを入れて終了が分かりやすくなる機能もあります。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/3/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  ポモドーロ・鳩タイマー
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="ポモドーロ・鳩タイマー"
                      src="../img/created-site3.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>ポモドーロ・テクニックをもとにして制作した Webツールです。時間がくると、鳩が教えてくれる仕様です。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/2/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  フィクション ドーナツ
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="ドーナツ専門店 フィクション ドーナツ"
                      src="../img/created-site2.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>実在しないドーナツ専門店の Webサイトを制作。コンテンツのライティングに注力しました。</p>
              </div>
            </a>
          </div>

          <div className={style.media}>
            <a className={style.media__link} href="https://moon-forest-design.github.io/created-sites/1/index.html" target="_blank" rel="noopener noreferrer">
              <div className={style.mediaEyeCatch}>
                <h3 className={style.mediaTitle}>
                  フィクション歯科医院
                </h3>
                <figure className={style.mediaThumbnail}>
                    <StaticImage
                      alt="フィクション歯科医院"
                      src="../img/created-site1.jpg"
                      placeholder="Blurred"
                    />
                </figure>
              </div>
              <div className={style.mediaDescription}>
                <p>実在しない歯科医院の Webサイトを制作。トップページのファーストビューには、jQueryと CSSアニメーションを使用しました。</p>
              </div>
            </a>
          </div>


        </div>
      </section>


    </Layout>
  )
};

export default IndexPage;
